<template>
  <div class="conteiner">
    <main class="conteiner__content">
      <ContentPage
        v-for="(page, index) in pages"
        :key="index"
        :page="page"
        v-show="activePageIndex === index" />
    </main>

    <aside class="conteiner__panel">
      <PanelButton
        v-for="(button, index) in pages"
        :key="index"
        :label="button.label"
        :id="button.id"
        @click="setActivePage(index)" />
    </aside>
  </div>
</template>

<script>
import { onMounted, ref } from 'vue';
import ContentPage from './ContentPage.vue';
import PanelButton from './PanelButton.vue';
import gsap from 'gsap';

export default {
  components: {
    ContentPage,
    PanelButton
  },
  setup() {
    const activePageIndex = ref(0);
    const pages = ref([
      { id: 'about-me', label: 'Кто я' },
      { id: 'portfolio', label: 'Опыт' },
      { id: 'skills', label: 'Скиллы' },
      { id: 'other', label: 'Контакты' }
    ]);

    const setActivePage = (index) => {
      activePageIndex.value = index;
    };

    onMounted(() => {
      const buttons = document.querySelectorAll('.panel-button'); 
      const pageElements = document.querySelectorAll('.content__page');

      gsap.from('.conteiner__panel', {
        duration: 1,
        z: 100,
        opacity: 0,
        ease: 'power2.out',
        delay: 1.5
      });

      gsap.from('.conteiner__content', {
        duration: 1,
        z: 100,
        opacity: 0,
        ease: 'power2.out',
        delay: 1.5
      });

      buttons.forEach((button) => {
        gsap.from(button, {
          duration: 0.5,
          opacity: 0.5,
          stagger: 1,
          delay: 1
        });
      });

      const ANGLE = 0.7;
      const TRANSITION_DURATION = 0.3;

        pageElements.forEach((page) => {
          page.style.transition = `transform ${TRANSITION_DURATION}s ease`;

            page.addEventListener('mouseout', () => {
              page.style.transition = `transform ${TRANSITION_DURATION}s ease`;
              page.style.transform = 'perspective(400px) rotateX(0deg) rotateY(0deg) translateZ(0px)';
            });
          
            page.addEventListener('mousemove', (e) => {
              const w = page.clientWidth;
              const h = page.clientHeight;
              const y = ((e.offsetX - w * 0.5) / w) * ANGLE;
              const x = -((e.offsetY - h * 0.5) / h) * ANGLE;
              page.style.transition = 'none';
              page.style.transform = `perspective(400px) rotateX(${x}deg) rotateY(${y}deg)`;
            });
          });



    });

    return {
      activePageIndex,
      pages,
      setActivePage
    };
  }
};
</script>

<style scoped>
.panel-button {
  flex-grow: 1; 
  flex-basis: 100%; 
}
</style>
